import React from 'react';
import Paper from 'material-ui/Paper';
import FloatingActionButton  from 'material-ui/FloatingActionButton';
import ContentAdd from 'material-ui/svg-icons/content/add';
import moment from 'moment';
import Gasto from './Gasto'
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow,
    TableRowColumn,
  } from 'material-ui/Table';
let e = React.createElement;
class ListaCategoria extends React.Component{
	constructor(props) {
    super(props);
    this.state = {
		gastosArr: [{
			categoria: 'Celpe',
			data: new Date(),
			id: 1
			},
			{
				categoria: 'Compesa',
				data: new Date(),
				id: 2
			}
		],
		openDialog: false
	};
	this.bot = this.bot.bind(this);
  }
  	bot (){

	}
	render (){
		return (
			e('div', {className: 'categoria-container'},
				e('h1',  {className: 'categoria-header'}, 'Lista de Gastos'),
				e(Paper, {className: 'categoria-body'},
					e(Table, {style: {marginBottom: '20px'}},
						e(TableHeader, null,
							e(TableRow, null,
								e(TableHeaderColumn, null, 'Categoria'),    
								e(TableHeaderColumn, null, 'Data do Pagamento')
							)
						),
						
						e(TableBody, null,
							this.state.gastosArr.map((item, index) => {
								return e(TableRow, {key: index},
									e(TableRowColumn, null, item.categoria),
									e(TableRowColumn, null, moment(item.data).format('DD/MM/YYYY'))
								)
							})
							
						)
					),
					e(FloatingActionButton , {onClick: () => {this.child.openDialog()}, style: {marginTop: '30px', float: 'right'}, secondary: true}, 
						e(ContentAdd, null)
					),
					e(Gasto, {ref: instance => { this.child = instance; }})
				)
			)
		);
	}

}

export default ListaCategoria;


